<template>
  <div class="login">
    <div class="login-left">
      <img src="../../assets/beijing.png">
    </div>
    <div class="login-right">
      <form action>
        <div class="content">
          <h3>上海仁济医院转诊系统</h3>
          <label for="user">账号</label>
          <input  v-model="userName" type="text" name="user" class="log-input" placeholder="请输入用户名/邮箱/手机号">
          <br>
          <label for="pwsd">密码</label>
          <input type="password" name="pwsd" class="log-input" placeholder="请输入密码">
          <!-- <router-link  to="/home"> -->
          <el-button type="primary" class="login-btn" @click="loginAction">登录</el-button>
          <!-- </router-link> -->
        </div>
      </form>
    </div>
  </div>
</template>
<style>
html,
body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}
#app {
  margin: 0;
}
.login {
  display: flex;
  height: 100vh;
}
.login-left {
  width: 58%;
}
.login-left img {
  width: 100%;
  height: 100vh;
}
.login-right {
  padding: 0 100px;
  display: flex;
  align-items: center;
}
.content {
  margin: auto;
  text-align: left;
}
.log-input {
  height: 25px;
  border: none;
  width: 100%;
  outline: none;
  border-bottom: 2px solid #58628a;
  margin-bottom: 30px;
  margin-top: 10px;
  font-size: 16px;
}
.login-btn {
  width: 100%;
  margin-top: 20px;
}
h3 {
  font-size: 20px;
  color: #576b95;
  margin-bottom: 70px;
  margin-top: 0;
}
</style>
<script>
export default {
  data() {
    return {
      userName:""

    }
  },
  methods: {
    
    loginAction: function() {

      if(this.userName == "admin"){

        this.$router.push({path: '/home/manage'});

      }else if(this.userName == "user"){

        this.$router.push({path: '/home/user'});
      }
    }
  }
};
</script>




